import React, { useEffect, useState } from 'react';

function Projectpage(contentplaceholder) {
    const [itemStyle, setItemsStyle] = useState(styles.projectviewFalse)

    useEffect(() => {
        if (contentplaceholder === true) {
            setItemsStyle(styles.projectviewTrue)
        }
        else {
            setItemsStyle(styles.projectviewFalse)
        }
    }, [0])
    return (
        <div style={itemStyle}>
            <div style={{ fontSize: "25px", top: "50px" }} >MY PROJECTS</div>
            <div style={{ fontSize: "25px" }}>SOME OF OUR WORKS</div>
            <div style={{ margin: "10px" }}>
                Use Pre-built Technology support for Business
                A business or company needs to run in a structured model depending on what are your product and services . But if you want more better result I want you to use the available pre-made Technology (hardware and software).So you don't need to use your excess money in developing it for big money if u can use what is available already .Make sure you use it the right way . If You want to hire me .leave a message</div>
        </div>
    );
}


const styles = ({
    projectviewTrue: {
        display: "flex",

        marginLeft: "270px",
        marginRight: "270px"
    },
    projectviewFalse: {
        display: "flex",
        flexDirection: "column",


    }

})

export default Projectpage;