import React, { } from 'react';
import Homepage from '../../pages/Homepage';

import Projectpage from '../../pages/Projectpage';

function ContentContainer({ contentplaceholder }) {


    return (
        <div style={styles.container}>
            <div> <Homepage contentplaceholder={contentplaceholder} /></div>
            <div> <Projectpage contentplaceholder={contentplaceholder} /></div>

        </div>

    );
}

const styles = ({

    container: {
        // backgroundColor: "red",
        display: "flex",
        flexDirection: "column",
        alignItems: "flex-end",
        alignItems: "center",
        justifyContent: "center",
        marginLeft: "auto",
        borderRadius: "55px",
        // backgroundColor: Colors.homeContentcolor,
        flex: "wrap",
        borderRadiusTopRight: "55px"

    }


})


export default ContentContainer;