import React, { useState, useEffect } from 'react';
import Colors from '../smallcomponents/Color';
import WindowSize from '../smallcomponents/WindowSize';
// import ContentContainer from './ContentContainer';

// export const LargeScreenContex = React.createContext({ contentplaceholder: false });

function Sidebar({ menuToggle }) {

    const [currentStyle, setCurrentStyle] = useState(styles.sidebar)

    const [windowsize, setWindowSize] = useState({ "height": window.innerHeight, "width": window.innerWidth })

    const [contentplaceholder, setContentPlaceHolder] = useState(false)



    useEffect(() => {
        const handlewindowResize = () => {
            setWindowSize({ "height": window.innerHeight, "width": window.innerWidth })
        }
        window.addEventListener('resize', handlewindowResize);

        return () => {
            window.removeEventListener("resize", handlewindowResize);


        }

    })


    useEffect(() => {

        const menutoggler = () => {
            if (menuToggle === true) {
                setCurrentStyle(styles.smallscreen)

            }
            else {
                setCurrentStyle(styles.empty)

            }
            return (() => {

            })

        }
        const menutogglerMedium = () => {
            if (menuToggle === true) {
                setCurrentStyle(styles.mediumScreen)

            }
            else {
                setCurrentStyle(styles.empty)

            }
            return (() => {

            })

        }

        //toggle is only for smaller dimension screen size
        if (window.matchMedia(WindowSize.small).matches) {
            menutoggler();
            setContentPlaceHolder(false)

        } else if (window.matchMedia(WindowSize.medium).matches) {
            setCurrentStyle(styles.mediumScreen)
            menutogglerMedium();
            setContentPlaceHolder(false)


        }
        else if ((window.matchMedia(WindowSize.large).matches)) {
            setCurrentStyle(styles.largeScreen)
            setContentPlaceHolder(true)

        }
        else {
            setCurrentStyle(styles.mediumScreen)
            setContentPlaceHolder(false)



            // alert("resize of window failed")
        }

    }, [windowsize, menuToggle])




    return (



        <>
            <div style={currentStyle}>
                <div style={{
                    position: "fixed",
                    display: "flex",
                    marginTop: "10px",
                    backgroundColor: "green",
                    height: "56px",
                    borderRadius: "7px",
                    alignItems: "center",
                    width: "180px"
                }}>
                    <div>
                        <div style={{
                            display: "flex",
                            flexDirection: 'row',
                            fontWeight: "bolder",
                            color: Colors.fontcolor,
                            fontSize: "15px", width: "100%",
                            padding: "10px",
                            justifyContent: "space-around"
                        }}>
                            <div>Window </div>
                            <div>Height </div>
                            <div>Width </div>
                        </div>


                        <div style={{
                            display: "flex",
                            flexDirection: 'row',
                            fontWeight: "normal",
                            alignItems: "center",
                            color: Colors.fontcolor,
                            justifyContent: "space-around"
                        }}>
                            <div style={styles.windowsizeTitle}> pixels </div>
                            <div style={{ color: Colors.primary }}>{windowsize.height}px</div>
                            <div style={{ color: Colors.primary }}>{windowsize.width}px</div>


                        </div>
                        {/* ///////////////////////// */}
                        <div style={{ textAlign: 'center', fontWeight: "bolder", fontSize: "22px", top: "25px" }}>KUMAR DULAL</div>
                        {/* ///////////////// */}
                    </div>




                </div>


            </div>


            {/* <div>


                <ContentContainer contentplaceholder={contentplaceholder} />

            </div> */}
        </>
    );

}

const styles = ({

    //Initial screen rendering style
    sidebar: {
        position: "relative",
        display: "flex",
        flexDirection: "column",
        width: "200px",
        backgroundColor: "lightblue",
        height: "100vh",
        borderTopRightRadius: "25px",
        top: "100px",
        justifyContent: "flex-start",
    },

    windowsizeTitle: {
        fontWeight: "bold"
    },



    empty: {
        display: "none"
    },


    //style accouding to  screen size
    smallscreen: {
        position: "fixed",
        right: "0px",
        display: "flex",
        width: "280px",
        backgroundColor: Colors.logocolor,
        height: "100vh",
        top: "2% ",
        border: `solid ${Colors.secondary}`,
        borderTopLeftRadius: "500px 20px",
        borderTopRightRadius: "2000px ",
        borderTopColor: Colors.third,
        borderLefttColor: Colors.third,



    },
    mediumScreen: {

        position: "fixed",
        right: "0px",
        display: "flex",
        width: "400px",
        backgroundColor: Colors.logocolor,
        height: "100vh",
        top: "12.2% ",
        border: `solid ${Colors.secondary}`,
        borderTopLeftRadius: "1000px 20px",
        borderTopRightRadius: "2000px ",
        borderButtonRightRadius: "2000px ",
        // borderRadius: '20%',
        borderTopColor: Colors.third,
        borderLefttColor: Colors.third,

    },
    largeScreen: {
        position: "fixed",
        display: "flex",
        width: "240px",
        backgroundColor: Colors.logocolor,
        height: "100vh",
        border: `solid ${Colors.try4}`,
        borderLeftColor: Colors.primary,
        top: "13.2%",
    },

    //this two is for content view responsible making deals bro 
    contentplaceholder: {
        //empty
    },
    contentplaceholder_midnsmallscreen: {
        left: "300px",
    }



})

export default Sidebar;